import './ToolsComponentStyle.css'
import MenuIcon from '@material-ui/icons/Menu'
import { Button, Menu, MenuItem, ThemeProvider } from '@material-ui/core'
import { menuTheme } from '../Theme/menuTheme'
import React from 'react'
import { useDispatch } from 'react-redux'
import { setTypeAsStopwatch, setTypeAsTimer } from '../store/forms/formsActions'
import { TYPES } from '../store/forms/formsTypes'

export default function ToolsComponent(){

    const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement|undefined>(null);

    const dispatch = useDispatch();

    const handleClick = (event:React.MouseEvent<HTMLButtonElement>) =>{

        setAnchorEl(event.currentTarget);
    }

    const handleOnTimerCLick = () =>{
        dispatch(setTypeAsTimer(TYPES.TIMER))
        setAnchorEl(null)
    }

    const handleOnStopwatchClick = () =>{
        dispatch(setTypeAsStopwatch(TYPES.STOPWATCH))
        setAnchorEl(null)
    }

    const handleClose = () =>{
        setAnchorEl(null);
    }


    return <div className={"tools-container"}>
        <ThemeProvider theme={menuTheme}>
            <div className={"menu-icon"}>
                <Button size="small" variant={"text"} onClick={handleClick}>
                    <ThemeProvider theme={menuTheme}>
                        <MenuIcon color={"primary"} fontSize={"large"}/>
                    </ThemeProvider>
                </Button>

                <Menu
                    id="simple-menu"
                    anchorEl={anchorEl}
                    keepMounted
                    open={Boolean(anchorEl)}
                    onClose={handleClose}
                    className={"menu"}
                >
                    <MenuItem onClick={handleOnTimerCLick}>Timer</MenuItem>
                    <MenuItem onClick={handleOnStopwatchClick}>Stopwatch</MenuItem>

                </Menu>
            </div>
        </ThemeProvider>
    </div>

}
