import { TYPES } from './formsTypes'


export interface STATE{
  state: TYPES
}

const initialState:STATE = {
  state: TYPES.STOPWATCH
}

// export const changeState:any = createAction("CHANGE")

export const timerReducer = (state = initialState, action:any)=>{
  switch (action.type){
    case "TIMER":
      return {...state, state:TYPES.TIMER}
    case "STOPWATCH":
      return {...state, state:TYPES.STOPWATCH}
    default:
      return state
  }
}



